import React, { useState, useRef } from "react";
import PhotoGrid from "react-photo-feed";
import { useGesture, usePinch } from "react-use-gesture";
import Contents from "./Contents";
// import QuickPinchZoom from "react-quick-pinch-zoom";
// import Hammer from "react-hammerjs";
import "./Image.Grid.css";
import playButton from "./assets/playButton.png";
import "react-photo-feed/library/style.css";

import golf from "./assets/golf.jpg";
import sampleVideo from "./assets/sample_video.mp4";
import shot from "./assets/shot.png";

function ImageGrid() {
    const [cardWidthIndex, setCardWidthIndex] = useState(4);
    const [pinchAble, setPinchAble] = useState(false);
    const [zoom, setZoom] = useState(0);
    // const touchTarget = React.useRef(null);
    const [pinchFlag, setPinchFlag] = useState({
        pinchInFlag: false,
        pinchOutFlag: false,
    });
    const { pinchInFlag, pinchOutFlag } = pinchFlag;
    const cardWidthArray = [1, 2, 3, 4, 6];
    let pointerCache = [];
    let prevDistance = -1;

    const handlePinchIn = () => {
        if (!pinchInFlag && cardWidthIndex < 4) {
            setPinchFlag({ pinchInFlag: true, pinchOutFlag: false });
            setCardWidthIndex(cardWidthIndex + 1);
        }
    };

    const handlePinchOut = () => {
        if (!pinchOutFlag && cardWidthIndex > 0) {
            setPinchFlag({ pinchInFlag: false, pinchOutFlag: true });
            setCardWidthIndex(cardWidthIndex - 1);
        }
    };

    const handlePinchEnd = () => {
        setPinchFlag({ pinchInFlag: false, pinchOutFlag: false });
    };

    const removeEvent = (e) => {
        const index = pointerCache.findIndex(
            (cache) => cache.pointerId === e.pointerId,
        );
        pointerCache.splice(index, 1);
    };

    const changeOptionsPinch = (able) => {
        setPinchAble(able);
    };

    const handlePointerDown = (e) => {
        pointerCache.push(e);
        // pointerCache가 1개일때는 div touch-action: none 아님,
        // none 부분을 state값으로 주기
        // if (pointerCache.length >= 2) {
        //     setPinchAble(true);
        // }
    };

    const handlePointerMove = (e) => {
        const index = pointerCache.findIndex(
            (cache) => cache.pointerId === e.pointerId,
        );
        pointerCache[index] = e;
        if (pointerCache.length === 2) {
            setZoom(
                Math.abs(pointerCache[0].clientX - pointerCache[1].clientX),
            );
        }
    };

    const handlePointerUp = (e) => {
        removeEvent(e);
        if (pointerCache.length < 2) {
            setPinchAble(false);
        }
    };

    // const bind = usePinch(({ offset: [d] }) => {
    //     setZoom(d);
    // });

    // useGesture(
    //     {
    //         onPinch: ({ pinching, offset: [d] }) => {
    //             // setPinchAble(pinching);
    //             setZoom(d);
    //         },
    //     },
    //     { domTarget: touchTarget, eventOptions: { passive: false } },
    // );

    return (
        <>
            <div>
                version 39, {pinchAble ? 1 : 0}, {zoom}
            </div>
            <div
                className="touch-area"
                // ref={touchTarget}
                onPointerDown={handlePointerDown}
                onPointerMove={handlePointerMove}
                onPointerUp={handlePointerUp}
                onPointerCancel={handlePointerUp}
                onPointerOut={handlePointerUp}
                onPointerLeave={handlePointerUp}
            >
                {/* <PhotoGrid
                    pinchInFlag={pinchInFlag}
                    pinchOutFlag={pinchOutFlag}
                    columns={cardWidthArray[cardWidthIndex]}
                    photos={Contents}
                    playButton={playButton}
                    changeOptionsPinch={changeOptionsPinch}
                /> */}
                <div className="hey" style={{ height: "100%" }}>
                    <div className="temp1">
                        <div className="temp2">
                            {/* <video>
                                <source
                                    src={sampleVideo}
                                    type="video/mp4"
                                ></source>
                            </video>
                            <img
                                className="button__in__video"
                                src={playButton}
                                alt="playButton"
                            /> */}
                        </div>
                        <div className="temp2">
                            {/* <video>
                                <source
                                    src={sampleVideo}
                                    type="video/mp4"
                                ></source>
                            </video>
                            <img
                                className="button__in__video"
                                src={playButton}
                                alt="playButton"
                            /> */}
                        </div>
                    </div>
                    {/* <div className="PhotoGrid__imageGridItem___hFxCm">
                        <div className="PhotoGrid__imageWrapper___1YTK9">
                            <img src={golf} alt="golf" />
                        </div>
                    </div>

                    <div className="PhotoGrid__imageGridItem___hFxCm">
                        <img
                            className="PhotoGrid__imageWrapper___1YTK9"
                            src={golf}
                            alt="golf"
                        />
                    </div>
                    <div className="PhotoGrid__imageGridItem___hFxCm">
                        <div className="PhotoGrid__imageWrapper___1YTK9">
                            <video>
                                <source
                                    src={sampleVideo}
                                    type="video/mp4"
                                ></source>
                            </video>
                            <img
                                className="button__in__video"
                                src={playButton}
                                alt="playButton"
                            />
                        </div>
                    </div>
                    <div className="PhotoGrid__imageGridItem___hFxCm">
                        <div className="PhotoGrid__imageWrapper___1YTK9">
                            <video>
                                <source
                                    src={sampleVideo}
                                    type="video/mp4"
                                ></source>
                            </video>
                            <img
                                className="button__in__video"
                                src={playButton}
                                alt="playButton"
                            />
                        </div>
                    </div>
                    <div className="PhotoGrid__imageGridItem___hFxCm">
                        <img
                            className="PhotoGrid__imageWrapper___1YTK9"
                            src={golf}
                            alt="golf"
                        />
                    </div>
                    <div className="PhotoGrid__imageGridItem___hFxCm">
                        <div className="PhotoGrid__imageWrapper___1YTK9">
                            <video>
                                <source
                                    src={sampleVideo}
                                    type="video/mp4"
                                ></source>
                            </video>
                            <img
                                className="button__in__video"
                                src={playButton}
                                alt="playButton"
                            />
                        </div>
                    </div> */}
                </div>
            </div>
        </>
    );
}

export default ImageGrid;
